import React, {useState} from "react";
import Affairs from "./Affairs";

// types
export type AffairPriorityType = 'low' | 'middle' | 'high'; // need to fix any
export type AffairType = {
    _id: number,
    name: string,
    priority: AffairPriorityType
}; // need to fix any
export type FilterType = "all" | AffairPriorityType;

// constants
const defaultAffairs: Array<AffairType> = [ // need to fix any
    {_id: 1, name: "React", priority: "high"},
    {_id: 2, name: "anime", priority: "low"},
    {_id: 3, name: "games", priority: "low"},
    {_id: 4, name: "work", priority: "high"},
    {_id: 5, name: "html & css", priority: "middle"},
    {_id: 6, name: 'World of Warcraft', priority: 'high'},
    {_id: 7, name: 'Dorohedoro', priority: 'middle'},
    {_id: 8, name: 'IRL', priority: 'low'}

];

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: FilterType): any => { // need to fix any
    let newAffairs =  filter === 'low'
            ? affairs.filter(a => a.priority === 'low')
            : filter === 'middle'
            ? affairs.filter(a => a.priority === 'middle')
            : filter === 'high'
            ? affairs.filter(a => a.priority === 'high')
            : affairs
    return newAffairs
     // need to fix
}
export const deleteAffair = (affairs: Array<AffairType>, _id: number): any => { // need to fix any
    let newAffairs = affairs.filter(a => a._id !== _id)
    return newAffairs
    ; // need to fix
}

function HW2() {
    const [affairs, setAffairs] = useState< Array <AffairType> >(defaultAffairs); // need to fix any
    const [filter, setFilter] = useState<FilterType>("all");

    const filteredAffairs = filterAffairs(affairs, filter);
    const deleteAffairCallback = (_id: number) => setAffairs(deleteAffair(affairs, _id)); // need to fix any

    return (
        <div>
            <hr/>
            homeworks 2

            {/*should work (должно работать)*/}
        
            <Affairs 
                data={filteredAffairs}
                setFilter={setFilter}
                deleteAffairCallback={deleteAffairCallback}
            />
            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeAffairs/>*/}
            <hr/>
        </div>
    );
}

export default HW2;
